@import url(https://fonts.googleapis.com/css?family=News+Cycle:400,700);
@import url(https://fonts.googleapis.com/css?family=Lato:400,700,400italic,700italic);
@import url(https://fonts.googleapis.com/css?family=Cousine);
@import url(https://fonts.googleapis.com/css?family=Tinos);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Monospace Font: Cousine */
.svg {
  font-family: 'Cousine', monospace; }

/* Verovio Text Font: Tinos */
.g.page-margin {
  font-family: 'Tinos', 'Times New Roman', 'Times', 'Liberation Serif', serif; }

/* Verovio SMuFL Font: Bravura */
html {
  font-family: Bravura;
  background: #fff;
}

.dt {
  font-weight: bold;
}

.q {
  font-style: italic;
}

.highlightedNote {
  fill: blue;
}

.passedNote {
  fill: forestgreen !important;
}

.semiPassedNote {
  fill: #cccc33;
}

.failedNote {
  fill: #cc0000;
}

.labelAbbr {
  display: none;
}

.label {
  display: none;
}

.syl {
  display: none;
}

.svg {
  background-color: beige;
}
.btn{
  --height: 100%;
  font-size: 1vw;
}

.btn2{
  --height: 100%;
  font-size: 1vw;
}

.swiper-slide.swiper-slide {
  background-color: beige;
}

.buttons {
  justify-content: space-around;
  width: 33%;
}

.toast {
  position: fixed;
  box-sizing: border-box;
  color: brown;
  font-size: 43px;
  width: 70px;
  height: 70px;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.noteDisplay {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.picker-wrapper.sc-ion-picker-ios {
  bottom: auto;
  left: auto;

}

.custom-class .action-sheet-wrapper {
  left: auto;
}

.picker-columns .picker-above-highlight {
  opacity: 1;
  --background: beige;
  color: black;
}

.picker-columns .picker-below-highlight {
  opacity: 1;
  --background: beige;
}

.picker-columns,
.picker-ios .picker-toolbar {
  background-color: beige;
}

.picker-ios .picker-prefix,
.picker-ios .picker-suffix,
.picker-ios .picker-opt.picker-opt-selected {
  background-color: beige;
  color: #000000;
  font-family: Montserrat-Medium, serif;
}
.picker-ios .picker-opt {
  background-color: beige;
  color: #000000;
  font-family: Montserrat-Medium, serif;
  z-index: 30;
}

.picker-ios .picker-button,
.picker-ios .picker-button.activated {
  background-color: beige;
  color: #3380ff;
  font-family: Montserrat-Regular, serif;
}

.menu-side-end .list-ios {
  --background: beige;
  background-color: beige;
}

.menu-side-end .list-ios .item {
  --background: beige;
  background-color: beige;
}

.menu-side-end  {
  --background: beige;
}

.header1 {
  width: 100%;
  height: 6vh;
  object-fit: contain;
}

.swiper-slide {
  will-change: transform;
}

.parent .swiper-slide {
  will-change: transform;
}

